import {useState} from 'react'
import classNames from 'classnames'
import Text from 'components/Text'
import Checkbox from 'components/Checkbox'
import Tag from 'components/Tag'
import Range from 'components/Range'
import {Table, Thead, Tbody, Trow, Tcell} from 'components/Table/index'
import style from './style.module.scss'

const toThousandString = (number) => (number >= 10000 ? `${number / 1000}K` : number)

const TabPanel = ({headers, tableData}) => {
  const [state, setState] = useState(tableData)
  const [sortValue, setSortValue] = useState({
    order: null,
    key: null,
  })

  const onHeaderClick = (key) => {
    const stateCopy = [...state]
    const isSameKey = sortValue.key === key
    const newOrder = isSameKey && sortValue.order === 'asc' ? 'desc' : 'asc'
    stateCopy.sort((a, b) => {
      if (a[key] > b[key]) {
        return newOrder === 'asc' ? -1 : 1
      }
      if (a[key] < b[key]) {
        return newOrder === 'asc' ? 1 : -1
      }
      return 0
    })
    setSortValue({key, order: newOrder})
    setState(stateCopy)
  }

  return (
    <div className={style['tab-panel']}>
      <div className={style['tab-panel__inner']}>
        <Table className={style.table}>
          <Thead>
            <Trow>
              {headers.map(({title, key}, index) => (
                <Tcell
                  key={`${title}-${index}`}
                  heading
                  className={classNames(index === 0 && style.sticky)}
                  scope="col"
                >
                  <button className={style['button--heading']} onClick={() => onHeaderClick(key)}>
                    <Text type="secondary" weight="bold" color="darkGray">
                      {title}
                    </Text>
                  </button>
                </Tcell>
              ))}
            </Trow>
          </Thead>
          <Tbody className={style.table__body}>
            {state.map((row, index) => {
              return (
                <Trow key={`${row.symbol}+${index}`} className={style.table__row}>
                  <Tcell heading className={style.sticky} scope="row">
                    <Text weight="heavy" color="blue">
                      {row.symbol}
                    </Text>
                  </Tcell>
                  <Tcell>
                    <Text type="secondary">{toThousandString(row.price)}</Text>
                  </Tcell>
                  <Tcell>
                    <Text type="secondary">{toThousandString(row.change)}</Text>
                  </Tcell>
                  <Tcell>
                    <Text type="secondary">{row.changePercent}</Text>
                  </Tcell>
                  <Tcell>
                    <Checkbox checked={row.alerts} readOnly />
                  </Tcell>
                  <Tcell>
                    <Text type="secondary">{toThousandString(row.volume)}</Text>
                  </Tcell>
                  <Tcell>
                    <Text type="secondary">{toThousandString(row.avgVolume)}</Text>
                  </Tcell>
                  <Tcell>
                    <Text type="secondary">{toThousandString(row.prevClose)}</Text>
                  </Tcell>
                  <Tcell>
                    <Range
                      min={row.dayRange.min}
                      max={row.dayRange.max}
                      value={row.dayRange.value}
                      startLabel={toThousandString(row.dayRange.min)}
                      endLabel={toThousandString(row.dayRange.max)}
                    />
                  </Tcell>
                  <Tcell>
                    <Tag type={row.quant.style}>{row.quant.value}</Tag>
                  </Tcell>
                  <Tcell>
                    <Tag type={row.authors.style}>{row.authors.value}</Tag>
                  </Tcell>
                  <Tcell>
                    <Tag type={row.sellSide.style}>{row.sellSide.value}</Tag>
                  </Tcell>
                </Trow>
              )
            })}
          </Tbody>
        </Table>
      </div>
    </div>
  )
}

export default TabPanel
