import {useState} from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import style from './style.module.scss'

const Tabs = (props) => {
  const {tabs, initial, className} = props
  const [state, setState] = useState(initial || tabs[0].id)
  const activeTab = tabs.find((tab) => tab.id === state)

  return (
    <div className={style['tabs-wrapper']}>
      <div className={classNames(style.tabs, className)}>
        <div className={style.tabs__inner}>
          {tabs.map(({id, title}) => {
            return (
              <button
                key={id}
                role="tab"
                type="button"
                className={classNames(style.tab, id === state && style['tab--active'])}
                onClick={(e) => {
                  e.preventDefault()
                  setState(id)
                }}
                aria-selected={id === state}
              >
                {title}
              </button>
            )
          })}
        </div>
      </div>
      <div className={style['tab-panel']} role="tabpanel">
        {activeTab.renderContent()}
      </div>
    </div>
  )
}

Tabs.propTypes = {
  tabs: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      renderContent: PropTypes.func.isRequired,
    })
  ),
  initial: PropTypes.string,
  className: PropTypes.string,
}

export default Tabs
