import PropTypes from 'prop-types'
import classNames from 'classnames'
import style from './style.module.scss'

const Range = (props) => {
  const {min, max, value, startLabel, endLabel, className, innerClassName} = props
  const showValue = ((Number(value) - Number(min)) / (Number(max) - Number(min))) * 100
  return (
    <div className={classNames(style.range, className)}>
      {startLabel && <span className={style.range__label}>{startLabel}</span>}
      <span className={classNames(style.range__inner, innerClassName)}>
        <div className={style.range__track}></div>
        <div
          role="slider"
          aria-readonly
          className={style.range__mark}
          aria-valuemax={max}
          aria-valuemin={min}
          aria-valuenow={value}
          style={{left: `${showValue}%`}}
        ></div>
      </span>
      {endLabel && <span className={style.range__label}>{endLabel}</span>}
    </div>
  )
}

Range.propTypes = {
  min: PropTypes.number.isRequired,
  max: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
  className: PropTypes.string,
  innerClassName: PropTypes.string,
  startLabel: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  endLabel: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
}

export default Range
