import PropTypes from 'prop-types'
import classNames from 'classnames'
import style from './style.module.scss'

const Tag = (props) => {
  const {children, type = 'empty'} = props
  return (<span className={classNames(style.tag, style[`tag--${type}`])}>{children}</span>)
}

Tag.propTypes = {
  type: PropTypes.oneOf(['veryGood', 'good', 'neutral', 'bad', 'veryBad', 'empty']),
}

export default Tag
