import {forwardRef} from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import style from './style.module.scss'

const Button = forwardRef((props, ref) => {
  const {children, primary, startIcon, onlyIcon, type = 'button', onClick, className, ...other} = props
  return (
    <button
      data-testid="button"
      ref={ref}
      type={type}
      className={classNames(
        style.button,
        {
          [style['button--primary']]: primary,
          [style['button--with-icon']]: startIcon,
          [style['button--only-icon']]: onlyIcon,
        },
        className
      )}
      onClick={onClick}
      {...other}
    >
      <span className={style.button__inner}>
        {startIcon && <span className={style['button__start-icon']}>{startIcon}</span>}
        <span className={style.button__text}>{children}</span>
      </span>
    </button>
  )
})

Button.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]),
  primary: PropTypes.bool,
  startIcon: PropTypes.node,
  onlyIcon: PropTypes.bool,
  type: PropTypes.string,
  onClick: PropTypes.func,
}

export default Button
