import classNames from 'classnames'
import PropTypes from 'prop-types'
import {ReactComponent as Icon} from 'assets/checkbox-icon.svg'
import style from './style.module.scss'

const Checkbox = (props) => {
  const {onChange, className, checked = false, readOnly} = props

  return (
    <label className={classNames(style.checkbox, readOnly && style['checkbox--read-only'], className)}>
      <input
        type="checkbox"
        defaultChecked={checked}
        className={style.checkbox__input}
        onChange={onChange}
        disabled={readOnly}
      />
      <span className={style.checkbox__mark}>
        <Icon className={style.checkbox__icon} />
      </span>
    </label>
  )
}

Checkbox.propTypes = {
  onChange: PropTypes.func,
  checked: PropTypes.bool,
  className: PropTypes.string,
}

export default Checkbox
