import {forwardRef} from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import style from './style.module.scss'

const Text = forwardRef((props, ref) => {
  const {type = 'primary', size = 'small', weight = 'regular', color = 'black', tag = 'span', className, ...other} = props
  const Component = tag

  return (
    <Component
      ref={ref}
      className={classNames(
        style[`type-${type}`],
        style[`size-${size}`],
        style[`weight-${weight}`],
        style[`color-${color}`],
        className
      )}
      {...other}
    />
  )
})

Text.propTypes = {
  type: PropTypes.oneOf(['primary', 'secondary']),
  size: PropTypes.oneOf(['extraSmall', 'small', 'medium', 'large', 'extraLarge']),
  weight: PropTypes.oneOf(['regular', 'bold', 'heavy']),
  color: PropTypes.oneOf(['black', 'white', 'red', 'green', 'blue', 'gray', 'darkGray']),
}

export default Text
